body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.meme-container {
  display: block;
  width: 90%;
  margin: 10px auto;
  max-height: 300px;text-align: center;
}
.meme-container img {
  width: 150px;
  height: auto;
  display: inline-block;
  margin: 20px 1px;
  border: 1px solid black;
}
.meme-container img:hover {
  border: 4px solid red;
  border-radius: 4px;
}

.meme {
  display: flex;
  justify-content: center;
  width: 300px;
  margin: 50px auto;

}
.meme img {
  width: 300px;
  margin: 0;
  padding: 0;

}


.container {
  text-align: center;
  padding: 10px;
}

.button { 
  display: block;
  margin: 0 auto;
  padding: 5px 10px;
  border: 1px solid black;
}
.description {
  text-align: center;
  font-size: 18px;
  font-weight: 700;;
}